import * as React from 'react';
import { graphql } from 'gatsby';
import Layout from '~src/components/Layout/Layout';
import { PostFeed } from '~src/components/Sections/PostFeed/PostFeed';
import { Blog } from '~src/pages/__generated__/Blog';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import styles from './header.module.scss';

const BlogPage: React.FC<{ data: Blog }> = ({ data }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<Layout data={data}>
			<div className="inner">
				<header className={styles.pageHeader}>
					<h1 className="site-title  no-image">{formatMessage('navigation.blog')}</h1>
				</header>
				<PostFeed data={data} />
			</div>
		</Layout>
	);
};

export default BlogPage;
export const blogQuery = graphql`
	query BlogPage($locale: String!) {
		posts: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 20
			filter: { fields: { locale: { eq: $locale } }, frontmatter: { category: { eq: "blog" } } }
		) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					desc
					category
					featured
					tags
					image {
						childImageSharp {
							fluid(maxWidth: 700) {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
	}
`;
