import * as React from 'react';
import { Index } from '~src/pages/__generated__/Index';
import { NotFound } from '~src/pages/__generated__/NotFound';
import { Author } from '~src/templates/__generated__/Author';
import { Tag } from '~src/templates/__generated__/Tag';
import { Post } from '~src/types/Post';
import PostCard from '../PostCard/PostCard';
import styles from './PostFeed.module.scss';

export const PostFeed: React.FC<{ data: Index | NotFound | Author | Tag; isHome?: boolean }> = ({ data, isHome }) => {
	return (
		<div className={styles.posts}>
			<div className={styles.postFeed}>
				{(data.posts.nodes as Post[]).map((post: Post, i: number) => {
					return <PostCard post={post} i={i} key={i} isHome={isHome} />;
				})}
			</div>
		</div>
	);
};
