import * as React from 'react';
//import { Index_posts_nodes as Post } from '~src/pages/__generated__/Index';
import Link from '~src/i18n/components/Link';
import ImgSharp from '~src/components/utils/ImgSharp/ImgSharp';
import styles from './PostCard.module.scss';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import { Post } from '~src/types/Post';

const options = {
	year: 'numeric',
	month: 'short',
	day: 'numeric',
};

const PostCard: React.FC<{
	post: Post;
	i?: number;
	isHome?: boolean;
}> = ({ post, i, isHome }) => {
	const { intl } = useIntlHelper();
	const isFeatured = post.frontmatter.featured;
	return (
		<article className={styles.postCard + ` ` + ((isHome && isFeatured && 0 === i % 6 && styles.postCardLarge) || ``)}>
			<Link className={styles.postCardImageLink} to={`/${post.frontmatter.category}/${post.fields.slug}`}>
				<ImgSharp
					className={styles.postCardImage}
					alt={post.frontmatter.title}
					/* 	sizes="(max-width: 1000px) 400px, 700px" */
					fluid={post.frontmatter.image.childImageSharp.fluid}
				/>
			</Link>
			<div className={styles.postCardContent}>
				<Link className={styles.postCardContentLink} to={`/${post.frontmatter.category}/${post.fields.slug}`}>
					<header className={styles.postCardHeader}>
						<div className={styles.postCardPrimaryTag}>
							{post.frontmatter.tags[0] !== undefined &&
								intl.formatMessage({
									id: `tags.${post.frontmatter.tags[0]}.title`,
								})}
						</div>
						<h2 className={styles.postCardTitle}>{post.frontmatter.title}</h2>
					</header>
					<section className={styles.postCardExcerpt}>
						<p>{post.frontmatter.desc ? post.frontmatter.desc : post.excerpt}</p>
					</section>
				</Link>
				<footer className={styles.postCardMeta}>
					<div className={styles.postCardBylineContent}>
						<span className={styles.postCardBylineDate}>
							<time dateTime={post.frontmatter.date}>{intl.formatDate(post.frontmatter.date, options)}</time>{' '}
							{/* 							<span className="bull">â€¢</span> {intl.formatNumber(post.timeToRead)}{' '}
							{intl.formatMessage({ id: 'posts.timeToRead' })} */}
						</span>
					</div>
				</footer>
			</div>
		</article>
	);
};

export default PostCard;
